import React, {useState} from 'react';
import {  makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import Divider from '@material-ui/core/Divider'
import HomeIcon from '@material-ui/icons/Home';
import WhatshotIcon from '@material-ui/icons/Whatshot';
import ClassIcon from '@material-ui/icons/Class';
import GroupIcon from '@material-ui/icons/Group';
import ContactSupportIcon from '@material-ui/icons/ContactSupport';
import FolderOpenIcon from '@material-ui/icons/FolderOpen';
import ListItemText from '@material-ui/core/ListItemText';

import {
  BrowserRouter as Router,
  Link
} from "react-router-dom";
const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  appBar: {
    backgroundColor: '#E8EAF6',
    color: 'rgba(0, 0, 0, 0.87)',
  },
  menuButton: {
    marginRight: theme.spacing(1),
  },
  twitterButton: {
    marginRight: theme.spacing(1),
    color: 'white',
    backgroundColor: '#1E88E5'
  },
  instaIcon: {
    color: 'white',
    backgroundColor: '#EC407A',
  },
  title: {
    flexGrow: 1,
  },
  link: {
    textDecoration: 'none',
    color: 'rgba(0, 0, 0, 0.87)',
  }
}));


export default function FestivalBar() {
  const classes = useStyles();

  const [drawer, setDrawer] = useState(false);

  function toggleDrawer() {
    setDrawer(!drawer);

  }

  const sideList = side => (
      <div
        className={classes.list}
        role="presentation"
        onClick={toggleDrawer}
      >
        <List>
            <Link to="/" className={classes.link}>
              <ListItem button>
                <ListItemIcon> <HomeIcon /></ListItemIcon>
                <ListItemText primary="Home" />
              </ListItem>
            </Link>
            <Link to="/festival" className={classes.link}>
              <ListItem button>
                <ListItemIcon> <WhatshotIcon /></ListItemIcon>
                <ListItemText primary="Festival" />
              </ListItem>
            </Link>
            <Link to="/awards" className={classes.link}>
              <ListItem button>
                <ListItemIcon> <ClassIcon /></ListItemIcon>
                <ListItemText primary="Awards" />
              </ListItem>
            </Link>
            <Divider/>
            <Link to="/partners" className={classes.link}>
              <ListItem button>
                <ListItemIcon> <GroupIcon /></ListItemIcon>
                <ListItemText primary="Partners" />
              </ListItem>
            </Link>
            <Link to="/directory" className={classes.link}>
              <ListItem button>
                <ListItemIcon> <FolderOpenIcon /></ListItemIcon>
                <ListItemText primary="Directory" />
              </ListItem>
            </Link>
            <Link to="/contact" className={classes.link}>
              <ListItem button>
                <ListItemIcon> <ContactSupportIcon /></ListItemIcon>
                <ListItemText primary="Contact Us" />
              </ListItem>
            </Link>
        </List>
        <Divider />
          <a href="https://www.facebook.com/keepit100essex" className={classes.link} target="_blank" rel="noopener noreferrer">
          <ListItem button>
              <ListItemIcon> <FacebookIcon /></ListItemIcon>
              <ListItemText primary="Facebook" />
            </ListItem>
          </a>
          <a href="https://www.instagram.com/keepit100essex" className={classes.link} target="_blank" rel="noopener noreferrer">
            <ListItem button>
              <ListItemIcon> <InstagramIcon /></ListItemIcon>
              <ListItemText primary="Instagram" />
            </ListItem>
          </a>
      </div>
  );

  return (
    <div className={classes.root}>
      <AppBar position="static"  className={classes.appBar}>
        <Toolbar>
          <IconButton onClick={toggleDrawer}  edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" className={classes.title}>
            Keep it 100
          </Typography>
        </Toolbar>
      </AppBar>
      <SwipeableDrawer  classes={{paper: classes.appBar}} disableDiscovery="true" disableSwipeToOpen="true" open={drawer} onClose={toggleDrawer} anchor="left">
        {sideList('left')}
      </SwipeableDrawer>
    </div>
  );
}