import React from 'react';
import {Helmet} from 'react-helmet';
import {makeStyles} from '@material-ui/core/styles';
import Ripples from './ripples.svg';
import {Animated} from 'react-animated-css';
import { Typography } from '@material-ui/core';


const useStyles = makeStyles(theme => ({
    home: {
        backgroundImage: `url(${Ripples})`,
        height: '100vh',
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',

    },
    hero: {
        position: 'relative',
        top: theme.spacing(2),
        textAlign: 'center',
        color: 'white',

    }
}))

export default function Home() {
   const classes = useStyles()
    return (
        <div className={classes.home}>
            <Helmet>
                <title>Keep it 100</title>
            </Helmet>
            <Animated animationIn="flipInX" animationInDelay="500">
                <div className={classes.hero}>
                    <Typography style={{fontFamily: 'Eight Bit Dragon'}} variant="h3">We are Keep it 100.</Typography>
                </div>
            </Animated>
        </div>
    )
}